  .slicknav_menu {
    margin: 0;
    padding: 0;
  }
  .slicknav_icon-bar {
      background: #fff !important;
  }
  .slicknav_nav {
      background: $theme-toggle;
      float: right;
      margin-top: 11px;
      padding: 19px;
      width: 100%;
      border-bottom: 1px solid #eee;
  }
  .slicknav_nav a:hover {
      background: #F79960 none repeat scroll 0 0;
      border-radius: 0;
      color: #ffffff;
  }
  
  .slicknav_nav a {
      font-size: 14px;
      font-weight: 400;
      color: $slicknav-font;
      text-transform: capitalize;
  }
  
  .slicknav_nav .slicknav_arrow {
      float: right;
  }
  
  .slicknav_nav .slicknav_row:hover,
  .slicknav_nav .slicknav_row:hover .slicknav_arrow {
      border-radius: 0;
      background-color: #F79960;
      background-color: transparent;
      color: var(--hover__color);
  }
  .slicknav_btn {
      background-color: transparent;
      cursor: pointer;
      margin-bottom: -15px;
      position: relative;
      z-index: 99;
      border: none;
      border-radius: 3px;
      top: -23px;
      padding: 5px;
      right: 5px;
      margin-top: -5px;
  }
  .slicknav_menu .slicknav_icon {
      margin-right: 6px;
      margin-top: 3px;
      position: relative;
      right: 5px;
      top: -5px;
      padding-bottom: 3px;
  }
  .slicknav_nav .slicknav_arrow {
      float: right;
      font-size: 22px;
      position: relative;
      top: -9px;
  }
  .slicknav_menu .slicknav_nav a:hover {
