    margin-left: 0.75em;

    .current-language {
        cursor: pointer;
    }

    .fa-angle-down {
        margin-left: 0.5em;
    }

    .fa-check {
        margin-left: 1em;
        display: none;
    }

    .active {
        .fa-check {
            display: inline-block;
        }
    }
}
